import * as React from "react";
import {
  List,
  Datagrid,
  TextField,
  useDataProvider,
  Filter,
  SearchInput,
  useRefresh,
} from "react-admin";
import copy from "copy-to-clipboard";

const OrderList = (props) => {
  const PostFilter = (props) => (
    <Filter {...props}>
      <SearchInput source="q" alwaysOn />
    </Filter>
  );
  console.log("props are", props);
  const dataProvider = useDataProvider();
  const refresh = useRefresh();

  const ConfirmButton = ({ record }) => {
    return (
      <button
        className="btn btn-primary"
        onClick={async () => {
          let response = await dataProvider.editOrder({
            id: record.id,
            status: "complete",
          });
          refresh();
          console.log(response);
        }}
      >
        Confirm
      </button>
    );
  };
  const CancelButton = ({ record }) => {
    return (
      <button
        className="btn btn-danger"
        onClick={async () => {
          let response = await dataProvider.editOrder({
            id: record.id,
            status: "cancelled",
          });
          refresh();
          console.log(response);
        }}
      >
        Cancel
      </button>
    );
  };
  const CopyButton = ({ record, item }) => {
    return (
      <button
        className="btn btn-primary"
        onClick={async () => {
          copy(record[item]);
        }}
      >
        Copy {item}
      </button>
    );
  };

  const BalerCopyButtons = ({ record }) => {
    if (record.playerId) {
      return <CopyButton record={record} item="playerId" />;
    }
    return (
      <>
        <CopyButton record={record} item="emailOrNumber" />
      </>
    );
  };

  const BalerBodge = ({ record }) => {
    if (record.playerId) {
      return null;
    }
    return <CopyButton record={record} item="password" />;
  };

  return (
    <List {...props} filters={<PostFilter />}>
      <Datagrid>
        <TextField source="id" />
        <BalerCopyButtons />
        <BalerBodge />
        <TextField source="status" />
        <TextField source="orderedBy" />
        <TextField source="name" />
        <TextField source="price" />
        <TextField source="accountPlatform" />
        <TextField source="bkashNumber" />
        <ConfirmButton />
        <CancelButton />
      </Datagrid>
    </List>
  );
};

export default OrderList;
