import { Admin, Resource } from "react-admin";
import custmDataProvider from "./dataProvider/index";
import MyAuthProvider from "./authProvider/index";
import GameList from "./components/games/gameList";
import createGame from "./components/games/createGame";
import editGame from "./components/games/editGame";
import PackageList from "./components/packages/packageList";
import CreatePackage from "./components/packages/createPackage";
import EditPackage from "./components/packages/editPackage";
import AllOrderList from "./components/orders/allOrderList.js";
import "bootstrap/dist/css/bootstrap.min.css";
import PendingOrderList from "./components/orders/pendingOrderList";
import UserList from "./components/userList";

let dp = new custmDataProvider(
  "https://ingame-item-topup-api.herokuapp.com/api"
  //"http://localhost:80/api"
);
console.log(dp.getList());
let myap = new MyAuthProvider(
  "https://ingame-item-topup-api.herokuapp.com/api"
  //"http://localhost:80/api"
);

function App() {
  return (
    <Admin dataProvider={dp} authProvider={myap}>
      <Resource name="Pending Orders" list={PendingOrderList} title="" />
      <Resource name="All Orders" list={AllOrderList} title="" />
      <Resource name="users" list={UserList} title="" />
      <Resource
        name="games"
        list={GameList}
        create={createGame}
        edit={editGame}
      />
      <Resource
        name="packages"
        list={PackageList}
        create={CreatePackage}
        edit={EditPackage}
      />
      <Resource
        name="promos"
        list={(props) => <PackageList {...props} ispromo={true} />}
        create={(props) => <CreatePackage {...props} ispromo={true} />}
        edit={(props) => <EditPackage {...props} ispromo={true} />}
      />
    </Admin>
  );
}

export default App;
