import { getHeader } from "../dataProvider/session";

async function send(API_URL) {
  // Register Service Worker
  console.log("Registering service worker...");
  const register = await navigator.serviceWorker.register("/sw.js", {
    scope: "/",
  });
  console.log("Service Worker Registered...");

  // Register Push
  console.log("Registering Push...");
  const subscription = await register.pushManager.subscribe({
    userVisibleOnly: true,
    applicationServerKey: urlBase64ToUint8Array(publicVapidKey),
  });
  console.log("Push Registered...");

  // Send Push Notification
  console.log("Sending Push...");
  let response = await fetch(`${API_URL}/admin/subscribe`, {
    method: "POST",
    body: JSON.stringify(subscription),
    headers: getHeader(),
  });
  let resBody = await response.json();
  if (resBody.err) {
    console.log("what the fuck");
  }
  console.log("Push Sent...");
}

const publicVapidKey =
  "BEWG3Ph2NC3xw6QjLtR3-9VfoujvD9es4w3dxkVnsdnHTbUQb9tinOo8gG5K6w88Gh3Zbv7rbNMswDnyu-C1en0";

async function askUserPermission() {
  return await Notification.requestPermission();
}

async function login(API_URL, { username, password }) {
  const request = new Request(API_URL + "/auth/login", {
    method: "POST",
    body: JSON.stringify({
      username: username,
      password: password,
    }),
    headers: new Headers({ "Content-Type": "application/json" }),
  });
  let sersverResponse = await fetch(request);
  if (sersverResponse.status < 200 || sersverResponse.status >= 300) {
    return Promise.reject(Error(sersverResponse.statusText));
  }
  let body = await sersverResponse.json();

  console.log("the server responded with:", body);

  localStorage.setItem("id", body.id);
  localStorage.setItem("key", body.key);
  localStorage.setItem("privillage", "admin");

  // Check for service worker
  if ("serviceWorker" in navigator) {
    await askUserPermission();
    send(API_URL).catch((err) => console.error(err));
  }

  return Promise.resolve();
}

export default login;

function urlBase64ToUint8Array(base64String) {
  const padding = "=".repeat((4 - (base64String.length % 4)) % 4);
  const base64 = (base64String + padding)
    .replace(/\-/g, "+")
    .replace(/_/g, "/");

  const rawData = window.atob(base64);
  const outputArray = new Uint8Array(rawData.length);

  for (let i = 0; i < rawData.length; ++i) {
    outputArray[i] = rawData.charCodeAt(i);
  }
  return outputArray;
}
