import * as React from "react";
import {
  List,
  Datagrid,
  TextField,
  DateField,
  useDataProvider,
  useRefresh,
  Filter,
  SearchInput,
} from "react-admin";

const UserList = (props) => {
  const PostFilter = (props) => (
    <Filter {...props}>
      <SearchInput source="q" alwaysOn />
    </Filter>
  );
  const dataProvider = useDataProvider();
  const refresh = useRefresh();
  const UnblockButton = ({ record }) => {
    if (!record.isBlocked) {
      return null;
    }
    return (
      <button
        className="btn btn-primary"
        onClick={async () => {
          let response = await dataProvider.editUser({
            id: record.id,
            isBlocked: false,
          });
          refresh();
          console.log(response);
        }}
      >
        Unblock
      </button>
    );
  };
  const BlockButton = ({ record }) => {
    if (record.isBlocked) {
      return null;
    }
    return (
      <button
        className="btn btn-danger"
        onClick={async () => {
          let response = await dataProvider.editUser({
            id: record.id,
            isBlocked: true,
          });
          refresh();
          console.log(response);
        }}
      >
        Block
      </button>
    );
  };
  return (
    <List {...props} filters={<PostFilter />}>
      <Datagrid>
        <TextField source="id" />
        <TextField source="username" />
        <TextField source="email" />
        <DateField source="joinedAt" />
        <UnblockButton />
        <BlockButton />
      </Datagrid>
    </List>
  );
};

export default UserList;
