import { getHeader } from "../session";

async function updateOrder(API_URL, record) {
  let order = record;
  let url = `${API_URL}/admin/orders?id=${record.id}&&status=${record.status}`;

  let serverResponse = await fetch(url, {
    headers: getHeader(),
    method: "PATCH",
  });
  let responseBody = await serverResponse.text();
  console.log(responseBody);
  order.id = responseBody._id;
  return { data: order };
}

export default updateOrder;
