import { HttpError } from "react-admin";
import { getHeader } from "../session";

async function getOrders(API_URL, resource, params) {
  let url = `${API_URL}/admin/orders?page=${params.pagination.page}&&limit=${params.pagination.perPage}`;
  if (resource === "Pending Orders") {
    url += "&&status=Pending";
  }
  if (params.filter.q) {
    url += "&&id=" + params.filter.q;
  }
  const serverResponse = await fetch(url, {
    method: "GET",
    headers: getHeader(),
  });
  if (serverResponse.status === 401) {
    //return Promise.reject("unauthorized");
    return Promise.reject(
      new HttpError(({} && "unauthorized") || "unauthorized", 401, {})
    );
  }
  const resBody = await serverResponse.json();
  if (resBody.err) {
    console.error(resBody);
  }

  let orders = resBody.values.map((value) => {
    let order = value;
    order.id = value._id;
    order.orderedBy = value.orderedBy.username;
    order.name = "Deleted";
    order.price = "Deleted";
    if (value.package) {
      order.name = value.package.name;
      order.price = value.package.price;
    }
    return order;
  });
  return { data: orders, total: resBody.total };
}

export default getOrders;
