import { getHeader } from "../session";

async function getPackage(API_URL, game, params) {
  let url = `${API_URL}/get/packages?id=${params.id}`;
  const serverResponse = await fetch(url, {
    method: "GET",
    headers: getHeader(),
  });
  console.log("the srver response body", serverResponse);
  const resBody = await serverResponse.json();
  console.log("the body", resBody);
  if (resBody.err) {
    console.log(resBody);
  }

  let thepackage = resBody;
  thepackage.id = resBody._id;
  console.log("the final thepackage is:", thepackage);
  return { data: thepackage };
}

export default getPackage;
