import { getHeader } from "../session";

async function getUsers(API_URL, resource, params) {
  let url = `${API_URL}/admin/users?page=${params.pagination.page}&&limit=${params.pagination.perPage}`;
  if (params.filter.q) {
    url += "&&username=" + params.filter.q;
  }
  const serverResponse = await fetch(url, {
    method: "GET",
    headers: getHeader(),
  });
  const resBody = await serverResponse.json();
  if (resBody.err) {
    console.log(resBody);
  }

  let users = resBody.values.map((value) => {
    let user = value;
    user.id = value._id;
    return user;
  });
  return { data: users, total: resBody.total };
}

export default getUsers;
