import { getHeader } from "../session";

async function getPackages(API_URL, game) {
  let url = `${API_URL}/get/packages`;
  url += "?game=" + game;
  const serverResponse = await fetch(url, {
    method: "GET",
    headers: getHeader(),
  });
  const resBody = await serverResponse.json();
  if (resBody.err) {
    console.log(resBody);
  }
  console.log(resBody);

  let thepackages = resBody.map((value) => {
    let thepackage = value;
    thepackage.id = value._id;
    return thepackage;
  });
  console.log("Im returing", thepackages);
  return { data: thepackages, total: thepackages.length };
}

export default getPackages;
