import { getHeader } from "../session";

async function updateUPackage(API_URL, params) {
  let thepcakage = params.data;
  let url = `${API_URL}/admin/packages/${params.id}`;

  let serverResponse = await fetch(url, {
    headers: getHeader(),
    method: "PATCH",
    body: JSON.stringify({
      name: thepcakage.name,
      price: thepcakage.price,
      category: thepcakage.category,
    }),
  });
  let responseBody = await serverResponse.text();
  console.log("response body 18:33", responseBody);
  thepcakage.id = responseBody._id;
  return { data: thepcakage };
}

export default updateUPackage;
