import * as React from "react";
import {
  List,
  Datagrid,
  TextField,
  EditButton,
  DeleteButton,
} from "react-admin";

function packageList(props) {
  console.log("props are", props);
  return (
    <List {...props}>
      <Datagrid>
        <TextField source="id" />
        {props.ispromo && <TextField source="category" />}
        <TextField source="name" />
        <TextField source="price" />
        <EditButton />
        <DeleteButton />
      </Datagrid>
    </List>
  );
}

export default packageList;
