import { getHeader } from "../session";

async function updateGame(API_URL, params) {
  console.log("The fucking params that is fucking me out is:", params);
  console.log("the fucking params is", params);
  let game = params.data;
  let url = `${API_URL}/admin/games/${params.id}`;

  let serverResponse = await fetch(url, {
    headers: getHeader(),
    method: "PATCH",
    body: JSON.stringify({
      name: game.name,
      image: game.image || "/img/default.jpg",
    }),
  });
  let responseBody = await serverResponse.text();
  console.log(responseBody);
  game.id = responseBody._id;
  return { data: game };
}

export default updateGame;
