import { getHeader } from "../session";

async function createGame(API_URL, params) {
  console.log("hello wrold from s/d/g/createGame.js");
  let game = params.data;
  let url = `${API_URL}/admin/games`;

  let serverResponse = await fetch(url, {
    headers: getHeader(),
    method: "POST",
    body: JSON.stringify({
      name: game.name,
      image: game.image || "/img/default.jpg",
    }),
  });
  let responseBody = await serverResponse.text();
  console.log(responseBody);
  game.id = responseBody._id;
  return { data: game };
}

export default createGame;
