import getGames from "./games/getGameList";
import getGame from "./games/getGame";
import createGame from "./games/createGame";
import updateGame from "./games/editGame";
import deleteGame from "./games/deleteGame";
import getPackages from "./packages/getPackageList";
import getPackage from "./packages/getPackage";
import deletePackage from "./packages/deletePackage";
import createPackage from "./packages/createPackage";
import getOrders from "./orders/getOrderList";
import updateUPackage from "./packages/editPackage";
import updateOrder from "./orders/editOrder";
import getUsers from "./users/getUserList";
import { getHeader } from "./session";

export default class customDataProvider {
  constructor(API_URL) {
    this.API_URL = API_URL;
  }
  async getList(resource, params) {
    try {
      if (resource === "games") {
        return await getGames(this.API_URL);
      }

      if (resource === "Pending Orders" || resource === "All Orders") {
        return getOrders(this.API_URL, resource, params);
        //return await getPackages(this.API_URL);
      }
      if (resource === "packages") {
        return getPackages(this.API_URL, "5fad192bb14f33d9676111d1", params);
      }
      if (resource === "promos") {
        return getPackages(this.API_URL, "5fafcf9dd37dec0017a8c84c", params);
      }
      if (resource === "users") {
        return getUsers(this.API_URL, resource, params);
      }
    } catch (err) {
      console.log(err);
    }
  }
  async getOne(resource, params) {
    try {
      if (resource === "games") {
        return getGame(this.API_URL, resource, params);
      }

      if (resource === "orders") {
        //return await getPackages(this.API_URL);
      }
      if (resource === "packages") {
        return await getPackage(this.API_URL, resource, params);
      }
      if (resource === "promos") {
        return await getPackage(this.API_URL, resource, params);
      }
    } catch (err) {
      console.log(err);
    }
  }
  async getMany() {}
  getManyReference() {}
  async create(resource, params) {
    try {
      if (resource === "games") {
        return await createGame(this.API_URL, params);
      }

      if (resource === "packages") {
        //return await getPackages(this.API_URL);
        return await createPackage(
          this.API_URL,
          params,
          "5fad192bb14f33d9676111d1"
        );
      }
      if (resource === "promos") {
        return await createPackage(
          this.API_URL,
          params,
          "5fafcf9dd37dec0017a8c84c"
        );
      }
    } catch (err) {
      console.log(err);
    }
  }
  async update(resource, params) {
    try {
      if (resource === "games") {
        return await updateGame(this.API_URL, params);
      }

      if (resource === "packages") {
        //return await getPackages(this.API_URL);
        return await updateUPackage(this.API_URL, params);
      }
      if (resource === "promos") {
        //return await getPackages(this.API_URL);
        return await updateUPackage(this.API_URL, params);
      }
    } catch (err) {
      console.log(err);
    }
  }
  updateMany() {}
  async delete(resource, params) {
    try {
      if (resource === "games") {
        return await deleteGame(this.API_URL, params);
      }
      if (resource === "packages") {
        //return await dele
        return await deletePackage(this.API_URL, params);
      }
      if (resource === "promos") {
        //return await dele
        return await deletePackage(this.API_URL, params);
      }
    } catch (err) {
      console.log(err);
    }
  }
  async deleteMany(resource, params) {
    try {
      if (resource === "games") {
        let ids = params.ids.map(async (id) => {
          let record = await deleteGame(this.API_URL, { id: id });
          return record.id;
        });
        return { data: ids };
      }
      if (resource === "packages") {
        //return await dele
        let ids = params.ids.map(async (id) => {
          let record = await deletePackage(this.API_URL, { id: id });
          return record.id;
        });
        return { data: ids };
      }
      if (resource === "promos") {
        //return await dele
        let ids = params.ids.map(async (id) => {
          let record = await deletePackage(this.API_URL, { id: id });
          return record.id;
        });
        return { data: ids };
      }
    } catch (err) {
      console.log(err);
    }
  }
  async editOrder(record) {
    return await updateOrder(this.API_URL, record);
  }
  async editUser(record) {
    let user = record;
    let state = "unblock";
    if (record.isBlocked) {
      state = "block";
    }
    let url = `${this.API_URL}/admin/users?id=${record.id}&&isBlocked=${state}`;

    let serverResponse = await fetch(url, {
      headers: getHeader(),
      method: "PATCH",
    });
    let responseBody = await serverResponse.json();
    console.log(responseBody);
    user.id = responseBody._id;
    return { data: user };
  }
}
