import { getHeader } from "../session";

async function deleteGame(API_URL, params) {
  let url = `${API_URL}/admin/games/${params.id}`;
  let response = await fetch(url, {
    headers: getHeader(),
    method: "DELETE",
  });
  console.log("the funckin server response", response);
  let responseBody = await response.json();
  responseBody.id = responseBody._id;
  responseBody._id = undefined;
  console.log("The effing responseBody:", responseBody);
  let returnObject = {};
  returnObject.data = responseBody;
  console.log("the effing return object", returnObject);
  return returnObject;
}

export default deleteGame;
