async function getGames(API_URL, resource, params) {
  console.log(resource, API_URL);
  const serverResponse = await fetch(`${API_URL}/get/games`, {
    method: "GET",
  });
  const resBody = await serverResponse.json();
  if (resBody.err) {
    console.log(resBody);
  }
  console.log(resBody);

  let games = resBody.map((value) => {
    let game = value;
    game.id = value._id;
    return game;
  });
  console.log("Im returing", games);
  return { data: games, total: games.length };
}

export default getGames;
