import { getHeader } from "../session";

async function createPackage(API_URL, params, game) {
  let thepackage = params.data;
  let url = `${API_URL}/admin/packages`;

  let serverResponse = await fetch(url, {
    headers: getHeader(),
    method: "POST",
    body: JSON.stringify({
      name: thepackage.name,
      price: thepackage.price,
      game: thepackage.game || game,
      category: thepackage.category,
    }),
  });
  let responseBody = await serverResponse.text();
  console.log(responseBody);
  thepackage.id = responseBody._id;
  return { data: thepackage };
}

export default createPackage;
