async function getGames(API_URL, resource, params) {
  const serverResponse = await fetch(`${API_URL}/get/games?id=${params.id}`, {
    method: "GET",
  });
  console.log("the srver response body", serverResponse);
  const resBody = await serverResponse.json();
  console.log("the body", resBody);
  if (resBody.err) {
    console.log(resBody);
  }

  let game = resBody;
  game.id = resBody._id;
  console.log("the final game is:", game);
  return { data: game };
}

export default getGames;
