import { getHeader } from "../session";

async function deletePackage(API_URL, params) {
  let url = `${API_URL}/admin/packages/${params.id}`;
  let response = await fetch(url, {
    method: "DELETE",
    headers: getHeader(),
  });
  let responseBody = await response.json();
  responseBody.id = responseBody._id;
  responseBody._id = undefined;
  let returnObject = {};
  returnObject.data = responseBody;
  return returnObject;
}

export default deletePackage;
